<template>
  <div>
    <v-toolbar flat>
      <v-toolbar-title>Mis Clientes</v-toolbar-title>
      <v-divider class="mx-4" inset vertical></v-divider>
      <v-toolbar-title>
        <v-icon @click="deleteAll()"> mdi-delete </v-icon></v-toolbar-title
      >
      <v-spacer></v-spacer>
      <v-dialog v-model="dialog" max-width="500px">
        <template v-slot:activator="{ on, attrs }">
          <v-btn color="primary" dark class="mb-2" v-bind="attrs" v-on="on">
            Mi nuevo cliente
          </v-btn>
        </template>
        <v-card>
          <v-card-title>
            <span class="text-h5">{{ formTitle }}</span>
          </v-card-title>

          <v-card-text>
            <v-container>
              <v-row>
                <v-col cols="12">
                  <v-text-field
                    v-model="editedItem.tradename"
                    label="Nombre Comercial"
                  ></v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-text-field
                    v-model="editedItem.turn"
                    label="Giro"
                  ></v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-text-field
                    v-model="editedItem.municipality"
                    label="Municipio"
                  ></v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-text-field
                    v-model="editedItem.whoAttendedUs"
                    label="Quien nos atendio?"
                  ></v-text-field>
                </v-col>
                <v-col cols="12">
                  <!--   <v-text-field
                    v-model="editedItem.date"
                    label="Fecha"
                  ></v-text-field> -->

                  <v-dialog
                    ref="dialog"
                    v-model="modal"
                    :return-value.sync="date"
                    persistent
                    width="290px"
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field
                        v-model="editedItem.date"
                        label="Fecha"
                        readonly
                        v-bind="attrs"
                        v-on="on"
                      ></v-text-field>
                    </template>
                    <v-date-picker v-model="editedItem.date" scrollable>
                      <v-spacer></v-spacer>
                      <v-btn text color="primary" @click="modal = false">
                        Cancel
                      </v-btn>
                      <v-btn
                        text
                        color="primary"
                        @click="$refs.dialog.save(date)"
                      >
                        OK
                      </v-btn>
                    </v-date-picker>
                  </v-dialog>
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click="close"> Cancelar </v-btn>
            <v-btn color="primary" dark @click="save"> Crear </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

      <v-dialog v-model="dialogDelete" max-width="500px">
        <v-card>
          <v-card-title class="text-h5"
            >Are you sure you want to delete this item?</v-card-title
          >
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click="closeDelete"
              >Cancel</v-btn
            >
            <v-btn color="blue darken-1" text @click="deleteItemConfirm"
              >OK</v-btn
            >
            <v-spacer></v-spacer>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-toolbar>

    <v-autocomplete
      class="mx-4"
      :items="items"
      item-text="tradename"
      label="Buscar a mi cliente por su nombre comercial"
    ></v-autocomplete>

    <div v-if="showScroll()">
      <v-virtual-scroll :items="items" :item-height="90" style="height: 83vh">
        <template v-slot:default="{ item }">
          <v-list-item color="primary">
            <v-list-item-content>
              <v-list-item-title>{{ item.tradename }}</v-list-item-title>

              <v-list-item-subtitle>{{ item.turn }}</v-list-item-subtitle>

              <v-list-item-subtitle>{{
                item.municipality + " - " + item.date
              }}</v-list-item-subtitle>
            </v-list-item-content>

            <v-list-item-action>
              <v-icon class="mr-10" @click="editItem(item)"> mdi-eye </v-icon>
            </v-list-item-action>
          </v-list-item>

          <v-divider></v-divider>
        </template>
      </v-virtual-scroll>
    </div>
    <div v-else>
      <p class="mx-4">
        Bienvenido.. puedes crear un cliente pulsando el botn: MI NUEVO CLIENTE
      </p>
    </div>
  </div>
</template>

<script>
export default {
  data: () => ({
    date: new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
      .toISOString()
      .substr(0, 10),
    modal: false,
    dialog: false,
    dialogDelete: false,
    items: [],
    editedIndex: -1,
    editedItem: {
      tradename: null,
      turn: null,
      municipality: null,
      whoAttendedUs: null,
      date: null,
    },
    defaultItem: {
      tradename: "",
      turn: "",
      municipality: "",
      whoAttendedUs: "",
      date: "",
    },
  }),

  computed: {
    formTitle() {
      return this.editedIndex === -1
        ? "Nuevo cliente"
        : "Editar datos del cliente";
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },

  created() {
    this.initialize();
  },

  methods: {
    initialize() {
      const save = localStorage.getItem("data");

      if (save) {
        this.items = JSON.parse(save);
      }

      /* if (!this.items) {
        this.items = [
          {
            tradename: "Frozen Yogurt",
            turn: "giro",
            municipality: "Benito Juarez",
            whoAttendedUs: 24,
            date: "03/12/12",
          },
          {
            tradename: "Ice cream sandwich",
            turn: 237,
            municipality: 9.0,
            whoAttendedUs: 37,
            date: 4.3,
          },
        ];
      } */
      //localStorage.clear();
    },

    deleteAll() {
      this.items = [];
      localStorage.clear();
    },

    showScroll() {
      if (this.items.length > 0) {
        console.log("true");
        return true;
      } else {
        console.log("false");
        false;
      }
    },

    editItem(item) {
      this.editedIndex = this.items.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      this.editedIndex = this.items.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      this.items.splice(this.editedIndex, 1);
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    save() {
      if (this.editedIndex > -1) {
        Object.assign(this.items[this.editedIndex], this.editedItem);
      } else {
        this.items.unshift(this.editedItem);
      }
      this.close();
      //Guardar en local Storage
      localStorage.setItem("data", JSON.stringify(this.items));
    },
  },
};
</script>
